import React, { Fragment, useEffect, useState } from "react";
import { AiFillDashboard } from "react-icons/ai";
import {
  MainItem,
  SideNavigation,
} from "../../components/Navigation/SideNavigation";
import VBarChart from "./ChartComponents/VBarChart";
import DashboardLabels from "./DashboardLabels";
import { StoreState } from "../../reducers";
import { connect } from "react-redux";
import { getAllRoom } from "../../actions/location";
import { PopupAlertData } from "../../components/Alert/Popup";
import {
  Auth,
  courseStoreInterface,
  GetApplicationPerPeriod,
  GetUserInfo,
  GetUserInfoInterface,
  loadCourses,
  loadEducationCategories,
  PushPopupAlert,
} from "../../actions";
import {
  ApplicationPeriodInterface,
  ApplicationSessionInterface,
  LocationRoomTableInterface,
  RoomLocationInterface,
} from "../../shared/interfaces";
import { education_category } from "../../interfaces/education_category";
import { DashboardReducerType } from "../../reducers/dashboard";
import { errorToText } from "../../utils/errors";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PositionAPI } from "../../interfaces/position";
import { getApplicationSessions, getPositions } from "../../actions/settings";
import { FaFilter, FaRegChartBar } from "react-icons/fa";
import { ApplicationStates } from "../../interfaces/application";
import ApplicationsGender from "./ChartComponents/ApplicationsGender";
import { LocationsInArray } from "../UserManagement/UserList";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import Courses from "../../components/Courses/Courses";
import { course } from "../../interfaces/course";
import ChartGender from "./ChartGender";

interface AppProps {
  auth: Auth;
  locations: RoomLocationInterface[];
  education_categories: education_category[] | null;
  dashboard: DashboardReducerType;
  systemPositions: PositionAPI[] | null;
  application_sessions: ApplicationSessionInterface[];
  course: courseStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  getAllRoom: Function;
  GetUserInfo: Function;
  GetApplicationPerPeriod: Function;
  getPositions: Function;
  getApplicationSessions: Function;
  loadCourses: (callback: (loading: boolean) => void) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const Index: React.FC<AppProps> = (props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingEducation, setLoadingEducation] = useState<boolean>(true);
  const [loadingApplications, setLoadingApplications] =
    useState<boolean>(false);
  const [thisEducation, setThisEducation] = useState<education_category | null>(
    null
  );
  const [thisPeriod, setThisPeriod] = useState<string>("");
  const [thisAppStatus, setThisAppStatus] = useState<ApplicationStates>(
    ApplicationStates.Shortlisted
  );
  const [thisExam, setThisExam] = useState<course | null>(null);
  const [openModal, setOpenModal] = useState<{ exam: boolean }>({
    exam: false,
  });

  useEffect(() => {
    setLoading(true);
    if (props.education_categories === null) {
      props.loadEducationCategories(setLoadingEducation);
    } else if (props.dashboard.userInfo === null) {
      setLoadingEducation(true);
      props.GetUserInfo((status: boolean, msg: string) => {
        setLoadingEducation(status);
        if (!status && msg.length > 0) {
          props.PushPopupAlert({
            title: errorToText(msg),
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (props.systemPositions === null) {
      setLoadingEducation(true);
      props.getPositions((status: boolean, msg: string) => {
        setLoadingEducation(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (!props.application_sessions.length) {
      props.getApplicationSessions((status: boolean, msg: string) => {
        setLoadingEducation(status);
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (thisEducation === null) {
      console.log("thisEducation: ", props.application_sessions);
      if (
        props.dashboard.applications === null &&
        props.education_categories.length > 0
      ) {
        setLoadingEducation(true);
        props.GetApplicationPerPeriod(
          props.education_categories[0],
          (status: boolean, msg: string) => {
            setLoadingEducation(status);
            if (!status && msg.length > 0) {
              props.PushPopupAlert({
                title: errorToText(msg),
                theme: PopupAlertTheme.danger,
              });
            }
          }
        );
      }
      setThisEducation(props.education_categories[0]);
    } else if (!props.locations.length) {
      console.log("location: ", thisEducation);
      props.getAllRoom((status: boolean, msg: string) => {
        if (status === true && msg.length > 0) console.error(msg);
      });
    } else if (props.dashboard.applications === null) {
      console.log("applicationsLoader: ", props.locations);
      setLoadingEducation(true);
      let active_period: ApplicationSessionInterface | undefined,
        periodId: string;
      if (!thisPeriod.length) {
        active_period = props.application_sessions.find(
          (item) => item.is_active
        );
        if (active_period) {
          console.log("active: ", active_period.application_session_id);
          setThisPeriod(active_period.application_session_id);
          periodId = active_period.application_session_id;
        } else {
          console.log("inactive");
          setThisPeriod(props.application_sessions[0].application_session_id);
          periodId = props.application_sessions[0].application_session_id;
        }
      } else {
        periodId = thisPeriod;
      }
      console.log({ periodId });
      props.GetApplicationPerPeriod(
        periodId,
        (status: boolean, msg: string) => {
          setLoadingEducation(status);
          if (!status && msg.length > 0) {
            props.PushPopupAlert({
              title: errorToText(msg),
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else {
      if (props.course.courses === null) {
        setLoadingEducation(true);
        props.loadCourses(setLoadingEducation);
      }
      console.log("elseLoader: ", props.course.courses);
      setLoading(false);
      setLoadingEducation(false);
    }
  }, [props, thisEducation, thisPeriod, thisPeriod.length]);

  const getPositionsByInstitute = (): {
    data: PositionAPI[];
    ids: string[];
  } => {
    let ids: string[] = [],
      data: PositionAPI[] = [];
    if (props.systemPositions && thisEducation) {
      data = props.systemPositions.filter(
        (item) =>
          item.education_institute_id.education_institute_id ===
          thisEducation.education_institute_id
      );

      data.forEach((element) => {
        ids.push(element.position_id);
      });
    }
    return { data, ids };
  };

  const getApplicationsByInsitutes = (
    status: "ALL" | ApplicationStates = "ALL"
  ): {
    data: ApplicationPeriodInterface[];
    ids: string[];
    uids: string[];
    positions: string[];
  } => {
    let result: ApplicationPeriodInterface[] = [],
      ids: string[] = [],
      uids: string[] = [];
    let position: string[] = [];

    if (props.dashboard.applications && thisEducation) {
      result = props.dashboard.applications.filter((item) =>
        getPositionsByInstitute().ids.includes(item.position)
      );

      for (const key in result) {
        if (Object.prototype.hasOwnProperty.call(result, key)) {
          if (position.includes(result[key].position)) continue;
          position.push(result[key].position);
        }
      }

      let data: ApplicationPeriodInterface[] = [];
      if (status === ApplicationStates.Shortlisted) {
        data = result.filter(
          (item) => item.application_status === ApplicationStates.Shortlisted
        );
      } else if (status === ApplicationStates.Rejected) {
        data = result.filter(
          (item) => item.application_status === ApplicationStates.Rejected
        );
      } else if (status === "ALL") {
        data = result;
      }

      data.forEach((element) => {
        ids.push(element._id);
        if (!uids.includes(element.user_id)) uids.push(element.user_id);
      });
      return { data, ids, uids, positions: position };
    }
    return { data: result, ids, uids, positions: position };
  };

  const getAllApplicantByInsitute = (gender: "ALL" | "M" | "F" = "ALL") => {
    let ids: string[] = [],
      data: GetUserInfoInterface[] = [];
    if (props.dashboard.userInfo && thisEducation) {
      if (gender === "M") {
        data = props.dashboard.userInfo.filter(
          (item) =>
            getApplicationsByInsitutes().uids.includes(item.user_id) &&
            item.user_info.sex === "M"
        );
      } else if (gender === "F") {
        data = props.dashboard.userInfo.filter(
          (item) =>
            getApplicationsByInsitutes().uids.includes(item.user_id) &&
            item.user_info.sex === "F"
        );
      } else {
        data = props.dashboard.userInfo.filter((item) =>
          getApplicationsByInsitutes().uids.includes(item.user_id)
        );
      }
    }
    return { data, ids };
  };

  const FC_DistinctDistrict = (): {
    distinctDistricts: LocationRoomTableInterface[];
    distinctDistrictNames: string[];
  } => {
    var flags: string[] = [],
      names: string[] = [],
      output: LocationRoomTableInterface[] = [],
      l: number = LocationsInArray(props.locations).length,
      i: number;
    for (i = 0; i < l; i++) {
      if (flags.includes(LocationsInArray(props.locations)[i].district_id))
        continue;
      flags.push(LocationsInArray(props.locations)[i].district_id);
      names.push(LocationsInArray(props.locations)[i].district_name);
      output.push(LocationsInArray(props.locations)[i]);
    }
    return { distinctDistricts: output, distinctDistrictNames: names };
  };

  const FC_CentersInDistrict = (district_id: string) => {
    let centersIdInDistrict: string[] = [];
    let districtData: LocationRoomTableInterface[] = LocationsInArray(
        props.locations
      ).filter((d) => d.district_id === district_id),
      l: number = districtData.length,
      i: number;
    for (i = 0; i < l; i++) {
      if (centersIdInDistrict.includes(districtData[i].center_id)) continue;
      centersIdInDistrict.push(districtData[i].center_id);
    }

    return centersIdInDistrict;
  };

  const setSelectedCourse = (selectedCourse: course | null) => {
    setThisExam(selectedCourse);
    setOpenModal({ exam: false });

    console.log({
      apps: props.dashboard.applications,
      centers: FC_CentersInDistrict(
        FC_DistinctDistrict().distinctDistricts[2].district_id
      ),
    });

    FC_ChartExam();
  };

  const FC_SelectedPositions = (
    gender: "ALL" | "M" | "F" = "ALL"
  ): {
    data: PositionAPI[];
    dataNames: string[];
    ids: string[];
    applications: ApplicationPeriodInterface[];
  } => {
    let ids: string[] = [],
      data: PositionAPI[] = [],
      selectedData: PositionAPI[] = [],
      dataNames: string[] = [],
      defaultApplications: ApplicationPeriodInterface[] =
        getApplicationsByInsitutes().data,
      applications: ApplicationPeriodInterface[] = [];
    if (props.systemPositions && thisEducation) {
      selectedData = props.systemPositions.filter(
        (item) =>
          item.education_institute_id.education_institute_id ===
          thisEducation.education_institute_id
      );

      selectedData.forEach((element) => {
        if (
          getApplicationsByInsitutes().positions.includes(
            element.position_id
          ) &&
          !ids.includes(element.position_id)
        ) {
          data.push(element);
          dataNames.push(element.position_title);
          ids.push(element.position_id);
        }
      });
    }
    for (const key in selectedData) {
      if (Object.prototype.hasOwnProperty.call(selectedData, key)) {
        defaultApplications
          .filter((item) => item.position === selectedData[key].position_id)
          .forEach((element) => {
            applications.push(element);
          });
      }
    }
    return { data: selectedData, dataNames, applications, ids };
  };

  const FC_Count = (
    ApplicationPeriod: ApplicationPeriodInterface[]
  ): {
    male: number;
    female: number;
  } => {
    // props.dashboard.userInfo;
    let male: number = 0,
      female: number = 0;
    for (const key in ApplicationPeriod) {
      if (Object.prototype.hasOwnProperty.call(ApplicationPeriod, key)) {
        if (props.dashboard.userInfo) {
          let result = props.dashboard.userInfo.filter(
            (item) => item.user_id === ApplicationPeriod[key].user_id
          );
          if (result[0].user_info.sex === "M") {
            male += 1;
          } else {
            female += 1;
          }
        }
      }
    }

    return { male, female };
  };

  const FC_ChartExam = (): {
    categoriesLabel: string[];
    dataLabels: boolean;
    yaxisText: string;
    tooltipText: string;
    series: {
      name: string;
      data: number[];
    }[];
  } => {
    let generalResult: {
      name: string;
      data: number[];
    }[] = [];
    if (props.dashboard.applications && thisExam) {
      let dataMale: number[] = [],
        dataFemale: number[] = [];

      let data: ApplicationPeriodInterface[] = [],
        dataExams: string[] = [],
        dataApplication: string[] = [];

      // LOOP FOR EACH DISTRICT ==================================
      for (const districtKey in FC_DistinctDistrict().distinctDistricts) {
        if (
          Object.prototype.hasOwnProperty.call(
            FC_DistinctDistrict().distinctDistricts,
            districtKey
          )
        ) {
          // LOOP FOR EACH APPLICATION ==================================
          for (const appKey in props.dashboard.applications) {
            if (
              Object.prototype.hasOwnProperty.call(
                props.dashboard.applications,
                appKey
              )
            ) {
              // LOOP FOR EACH APPLICATION EXAM ==================================
              for (
                let j = 0;
                j < props.dashboard.applications[appKey].exams.length;
                j++
              ) {
                if (
                  FC_CentersInDistrict(
                    FC_DistinctDistrict().distinctDistricts[districtKey]
                      .district_id
                  ).includes(
                    props.dashboard.applications[appKey].exams[j].center_id
                  ) &&
                  props.dashboard.applications[appKey].exams[j].course_id ===
                    thisExam.course_id
                ) {
                  if (
                    dataApplication.includes(
                      props.dashboard.applications[appKey]._id
                    ) ||
                    dataExams.includes(
                      props.dashboard.applications[appKey].exams[j].course_id
                    )
                  )
                    continue;
                  dataExams.push(
                    props.dashboard.applications[appKey].exams[j].course_id
                  );
                  dataApplication.push(
                    props.dashboard.applications[appKey]._id
                  );
                  data.push(props.dashboard.applications[appKey]);
                  continue;
                }
              }
            }
          }
        }

        console.log({
          district:
            FC_DistinctDistrict().distinctDistricts[districtKey].district_name,
          data,
          dataExams,
          series: FC_Count(data),
        });
        dataMale.push(FC_Count(data).male);
        dataFemale.push(FC_Count(data).female);
        data = [];
        dataExams = [];
        dataApplication = [];
      }
      generalResult = [
        {
          name: "Male",
          data: dataMale,
        },
        {
          name: "Female",
          data: dataFemale,
        },
      ];
    }

    return {
      categoriesLabel: FC_DistinctDistrict().distinctDistrictNames,
      dataLabels: false,
      yaxisText: "(Number of Applicants)",
      tooltipText: "Applicants",
      series: generalResult,
    };
  };

  if (loading === true || loadingEducation === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading Data...
      </div>
    );
  }

  // const FC_filterByDistrict = (
  //   roomTables: LocationRoomTableInterface[],
  //   filterValue: string
  // ): ApplicationPeriodInterface[] => {
  //   let centersIdInDistrict: string[] = [];
  //   let districtData: LocationRoomTableInterface[] = roomTables.filter(
  //       (d) => d.district_id === filterValue
  //     ),
  //     l: number = districtData.length,
  //     i: number;
  //   for (i = 0; i < l; i++) {
  //     if (centersIdInDistrict.includes(districtData[i].center_id)) continue;
  //     centersIdInDistrict.push(districtData[i].center_id);
  //   }
  //   // END GETTING CENTERS IN SPECIFIED DISTRICT ===================================

  //   // GETTING LIST OF CANDIDATES IN DISTRICT =========================
  //   let data: ApplicationPeriodInterface[] = [];
  //   let list_1: ApplicationPeriodInterface[] =
  //     documentReducer.candidatesInInstitute
  //       ? documentReducer.candidatesInInstitute.filter(
  //           (candidate) => candidate.details.length > 0
  //         )
  //       : [];
  //   let mailList: string[] = [];
  //   let list_2: ApplicationPeriodInterface[] = list_1.filter((candidate) =>
  //     candidate.details.filter((item) => item.files !== "")
  //   );
  //   let list: ApplicationPeriodInterface[] = [];
  //   for (let i = 0; i < list_2.length; i++) {
  //     if (mailList.includes(list_2[i].email)) continue;
  //     mailList.push(list_2[i].email);
  //     list.push(list_2[i]);
  //   }

  //   let result = list; // restaring list
  //   for (let i = 0; i < result.length; i++) {
  //     for (let j = 0; j < result[i].exams.length; j++) {
  //       if (centersIdInDistrict.includes(result[i].exams[j].center_id)) {
  //         data.push(result[i]);
  //         // action = false;

  //         // ======================================
  //         let mailList: string[] = [];
  //         let _data: ApplicationPeriodInterface[] = [];
  //         for (let i = 0; i < data.length; i++) {
  //           if (mailList.includes(data[i].email)) continue;
  //           mailList.push(data[i].email);
  //           _data.push(data[i]);
  //         }
  //         data = _data;
  //         // ======================================
  //       }
  //     }
  //   }

  //   return (result = data);
  // };

  return (
    <Fragment>
      <SideNavigation>
        {/* <Search data={[1, 2, 3]} /> */}
        {/* EDUCATION CATEGORY sections */}
        <span className="text-sm text-gray-600 font-semibold">
          Education Institute
        </span>
        {props.education_categories &&
          props.education_categories.length > 0 &&
          props.education_categories.map((education, index) => (
            <section
              key={education.education_institute_id + index}
              className={`mt-2 ${
                (loading || loadingEducation || loadingApplications) &&
                "cursor-not-allowed"
              }`}
              onClick={() =>
                !loading &&
                !loadingEducation &&
                !loadingApplications &&
                setThisEducation(education)
              }
            >
              <MainItem
                title={education.education_category_title}
                active={
                  thisEducation
                    ? education.education_institute_id ===
                      thisEducation.education_institute_id
                    : false
                }
              />
            </section>
          ))}
        {/* end EDUCATION CATEGORY sections */}

        {/* APPLICATIONS STATUS sections */}
        <span className="text-sm text-gray-600 font-semibold hidden">
          Application status
        </span>
        <section
          className={`mt-2 hidden ${
            (loading || loadingEducation || loadingApplications) &&
            "cursor-not-allowed"
          }`}
          onClick={() =>
            !loading &&
            !loadingEducation &&
            !loadingApplications &&
            setThisAppStatus(ApplicationStates.Shortlisted)
          }
        >
          <MainItem
            title={"Shortlisted"}
            count={
              getApplicationsByInsitutes(ApplicationStates.Shortlisted).data
                .length
            }
            active={
              thisAppStatus === ApplicationStates.Shortlisted ? true : false
            }
          />
        </section>
        <section
          className={`mt-2 hidden ${
            (loading || loadingEducation || loadingApplications) &&
            "cursor-not-allowed"
          }`}
          onClick={() =>
            !loading &&
            !loadingEducation &&
            !loadingApplications &&
            setThisAppStatus(ApplicationStates.Rejected)
          }
        >
          <MainItem
            title={"Rejected"}
            count={
              getApplicationsByInsitutes(ApplicationStates.Rejected).data.length
            }
            active={thisAppStatus === ApplicationStates.Rejected ? true : false}
          />
        </section>
        {/* END APPLICATIONS STATUS sections */}

        {/* <span className="text-sm text-gray-600 font-semibold">
          Application status
        </span>
        <section className="mt-2">
          <MainItem title={"Seats"} count={5} active={false} />
        </section> */}
      </SideNavigation>

      {/* HEADER TITLE */}
      <div className="mt-3 rounded flex flex-col md:flex-row md:justify-between items-center">
        <div className="flex flex-row items-center gap-3 mb-2">
          <AiFillDashboard className="text-6xl text-blue-600 hidden md:block" />
          <div className="flex flex-col">
            <span className="text-blue-600 text-3xl font-bold">
              Application Dashboard
            </span>
            <span className="text-gray-600 text-sm hidden md:block">
              Customization of question bank predefined settings including
              courses, chapters, question types, etc
            </span>
          </div>
        </div>
        <div>
          <select
            className={`bg-white text-gray-600 border border-gray-400 rounded p-2 focus:outline-none ${
              loadingApplications && "cursor-wait"
            }`}
            value={thisPeriod}
            disabled={loading || loadingApplications || loadingEducation}
            onChange={(e) => {
              setThisPeriod(e.target.value);
              setLoadingApplications(true);
              props.GetApplicationPerPeriod(
                e.target.value,
                (status: boolean, msg: string) => {
                  setLoadingApplications(status);
                  if (!status && msg.length > 0) {
                    props.PushPopupAlert({
                      title: errorToText(msg),
                      theme: PopupAlertTheme.danger,
                    });
                  }
                }
              );
            }}
          >
            {props.application_sessions.map((session, item) => (
              <option
                key={session.application_session_id + item}
                value={session.application_session_id}
              >
                {session.application_session_name}
              </option>
            ))}
          </select>
        </div>
      </div>
      {/* END HEADER TITLE */}

      <DashboardLabels
        applicants={getAllApplicantByInsitute().data.length}
        applications={getApplicationsByInsitutes().data.length}
        shortlisted={
          getApplicationsByInsitutes(ApplicationStates.Shortlisted).data.length
        }
        rejected={
          getApplicationsByInsitutes(ApplicationStates.Rejected).data.length
        }
        positions={getApplicationsByInsitutes().positions.length}
      />

      {/* CHARTS SECTIONS GENDER && AGE ======================================= */}
      <ChartGender getAllApplicantByInsitute={getAllApplicantByInsitute} />
      {/* END CHARTS SECTIONS GENDER && AGE ======================================= */}

      {/* CHARTS SECTIONS NUMBER OF APPLICATION - GENDER ==================================== */}
      {props.dashboard.userInfo && (
        <ApplicationsGender
          FC_SelectedPositions={FC_SelectedPositions}
          positionNames={FC_SelectedPositions().dataNames}
          userInfo={props.dashboard.userInfo}
        />
      )}
      {/* END CHARTS SECTIONS NUMBER OF APPLICATION - GENDER ================================ */}

      {/* CHARTS SECTIONS NUMBER OF APPLICATION - POSITION ==================================== */}
      <section className="my-3 bg-white">
        <div className="flex items-center justify-between px-3">
          <span className="text-gray-600 font-semibold text-lg">
            Number of Applicants - Exam
          </span>
          <div className="bg-white">
            <button
              className="flex items-center gap-2 text-gray-600 py-4 px-6 hover:text-blue-500 focus:outline-none border-b-2 font-medium border-blue-500"
              disabled={loading || loadingApplications || loadingEducation}
              onClick={() => setOpenModal({ exam: true })}
            >
              <FaFilter className="w-4 h-4" />{" "}
              {thisExam ? thisExam.course_name : "Filter"}
            </button>
            <nav className="flex flex-col sm:flex-row"></nav>
          </div>
        </div>
        {thisExam ? (
          <VBarChart
            categoriesLabel={FC_ChartExam().categoriesLabel}
            dataLabels={FC_ChartExam().dataLabels}
            yaxisText={FC_ChartExam().yaxisText}
            tooltipText={FC_ChartExam().tooltipText}
            series={FC_ChartExam().series}
          />
        ) : (
          <div
            className="flex flex-col items-center animate__animated animate__flash cursor-pointer text-gray-500 hover:text-gray-600"
            onClick={() =>
              !loading &&
              !loadingApplications &&
              !loadingEducation &&
              setOpenModal({ exam: true })
            }
          >
            <FaRegChartBar className="w-72 h-72" />
            <span className="text-4xl font-semibold">
              Choose course to view
            </span>
          </div>
        )}
      </section>
      {/* END CHARTS SECTIONS NUMBER OF APPLICATION - POSITION ================================ */}

      {/* modal control */}
      {openModal.exam && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            setOpenModal({ exam: false });
          }}
          displayClose={true}
          backDropClose={false}
          title="Choosing Exam"
          widthSizeClass={ModalSize.large}
          padding={{ title: true, body: false, footer: false }}
        >
          <Courses
            selected_course_id={thisExam ? thisExam.course_id : null}
            setSelectedCourse={setSelectedCourse}
          />
        </Modal>
      )}
      {/* end modal control */}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  locations,
  schoolLevels,
  dashboard,
  course,
  system_settings,
}: StoreState): {
  auth: Auth;
  locations: RoomLocationInterface[];
  education_categories: education_category[] | null;
  dashboard: DashboardReducerType;
  systemPositions: PositionAPI[] | null;
  course: courseStoreInterface;
  application_sessions: ApplicationSessionInterface[];
} => {
  return {
    auth: auth,
    locations: locations.roomLocation,
    education_categories: schoolLevels.education_categories,
    dashboard,
    course: course,
    systemPositions: system_settings.systemPositions,
    application_sessions: system_settings.applicationSessions,
  };
};

export default connect(mapStateToProps, {
  getAllRoom,
  loadEducationCategories,
  getPositions,
  GetUserInfo,
  GetApplicationPerPeriod,
  getApplicationSessions,
  loadCourses,
  PushPopupAlert,
})(Index);
